.languages {
    position: relative;
    padding: 5px 0;
    min-height: 30px;
    width: 16.66%;
    float: right;
    top: 5px;
    @include breakpoint(larges) {
        position: absolute;
        top: 30px;
        right: 0;
    }
    @include breakpoint(mediums) {
        top: 8px;
        right: 30px;
    }
    @include breakpoint(smalls) {
        display: none;
        &.languages_mobile {
            display: block;
        }
    }
    &__icons {
        position: absolute;
        left: auto;
        right: 20px;
        display: inline-block;
        .languages_mobile & {
            right: 0;
            width: 100%;
            text-align: center;
        }
    }
    &_icon {
        display: inline-block;
        margin-right: 3px;
        margin-left: 3px;
    }
    &.languages_mobile {
        width: 100%;
        position: relative;
        right: 0;
    }
    &__name {
        @extend %title;
        top: -8px;
    }
    &__link {
        display: block;
        margin-bottom: 5px;
        &-item{
            display: inline-block;
            position: relative;
        }
        &_right {
            text-align: right;
            right: 0;
        }
        &_left {
            text-align: left;
            left: 0;
        }
    }
}